import { Container } from 'components/common';
import { Wrapper,Grid } from './styles';
import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

const useStyles = makeStyles({
  root: {
    maxWidth: 345,
  },
  media: {
    height: 140,
  },
});
export const Projects = () => {
  const classes = useStyles();

  const bigProjects = {
    projects: [
      {
       // image: require(""),
        projectName: "Team Chat(WhatsApp Clone)",
        projectDesc: "A whatsapp clone made with MERN Stack Application using Pusher(Socket.io Alternative)",
        url : "https://github.com/themr97/RoomChat-WhatsappClone-FrontEnd",
        demourl : "https://github.com/themr97/RoomChat-WhatsappClone-FrontEnd",
      },
      {
      //  image: require(""),
        projectName: "Saayahealth 2",
        projectDesc: "Lorem ipsum dolor sit amet, consectetur adipiscing elit",
        url : "http://google.com",
        demourl : "http://google.com",
      },
    ],
  };

  return (
    <Wrapper as={Container} id="projects">
      <h1>Projects</h1>
      <Grid>
      {bigProjects.projects.map((project, i) => {
      return (
    <Card className={classes.root} key={i}>
      <CardActionArea>
        <CardMedia
          className={classes.media}
          // image={project.image}
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="h2">
          {project.projectName}
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            {project.projectDesc}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions>
        <a href={project.url} target="_blank" >GitHub</a>
        <a href={project.demourl} target="_blank" >Live Demo</a>
      </CardActions>
    </Card>
      );
    })}
    </Grid>
    </Wrapper>
  );
};